<script>
import Category from '../components/Category.vue';
import axios from 'axios';
import adVue from '../components/ad.vue';
import sliderGroup from '../components/sliderGroup.vue'
import carousel from '../components/carousel.vue'
export default {
    created() {
      let music = new Audio('http://soundbible.com/mp3/Air Plane Ding-SoundBible.com-496729130.mp3');
      music.play();
        this.$store.dispatch('loadCats');
        this.$store.dispatch('getads');
        axios.get('/getmostvisited/'+'all').then(res => {
            this.mostvisited = res.data.mostvisited;
            console.log(this.mostvisited)
            });
        axios.get('/getbestrated/'+'all').then(res => {
            this.bestrated = res.data.bestrated;
            });
        axios.get('/getNewestItems').then(res => {
            this.newestItems = res.data.newestItems;
            });
    },
    mounted() {
        axios.get('/getInfo').then(res => {
            const info = {...res.data.info};
            this.theme = info.theme;
            this.url = info.image;
            delete info['theme'];
            delete info['image'];
            this.texts = info;
        })
    },
    data() {
        return {
            textToEdit: null,
            theme:'red',
            mostvisited: [],
            bestrated: [],
            newestItems: [],
            url: null,
            file: null,
            menu: ['text25', 'text26', 'text27', 'text28'],
