            return this.$store.getters.cats;
            },
        adData() {
          console.log(this.$store.getters.ads);
          return this.$store.getters.ads;
        }
    },
    components: {
        appCategory: Category ,
        appAd: adVue,
        sliderGroup,
        appCarousel: carousel
    }, 
    methods: {
        gotoitems(cat) {
            this.$router.push({path: '/items/'+cat});
        },
        gotoItem(id) {
          this.$router.push({path: '/details/'+id});
        }
    }    
}
