<template>
     <body @mousewheel="()=>textToEdit = null"  id="page-top">
        <!-- Navigation-->
        <nav style="margin-top:50px" class="navbar navbar-expand-lg navbar-light fixed-top py-3" id="mainNav">
            <div class="container">
                <div class="collapse navbar-collapse" id="navbarResponsive">
                    <ul class="navbar-nav ml-auto my-2 my-lg-0">
                        <li @click="()=>textToEdit = 'text25'" class="nav-item"><a class="nav-link js-scroll-trigger" href="#about">{{texts.text25}}</a></li>
                        <li @click="()=>textToEdit = 'text26'" class="nav-item"><a class="nav-link js-scroll-trigger" href="#services">{{texts.text26}}</a></li>
                        <li @click="()=>textToEdit = 'text27'" class="nav-item"><a class="nav-link js-scroll-trigger" href="#portfolio">{{texts.text27}}</a></li>
                        <li @click="()=>textToEdit = 'text28'" class="nav-item"><a class="nav-link js-scroll-trigger" href="#contact">{{texts.text28}}</a></li>
                    </ul>
                </div>
            </div>
        </nav>
        <!-- Masthead-->
        <header :style="'background: linear-gradient(to bottom, rgba(92, 77, 66, 0.8) 0%, rgba(92, 77, 66, 0.8) 100%), url('+url+')'" class="masthead">
            <div class="container h-100">
                <input type="file" @change="imagePrev" style="display:none" ref="img"/>
                <a @click="$refs.img.click()" :style="'background-color:'+theme" class="btn btn-primary">Upload image</a>
                <div class="row h-100 align-items-center justify-content-center text-center">
                    <div @click="()=>textToEdit = 'text1'" class="col-lg-10 align-self-end">
                        <h1 class="text-uppercase text-white font-weight-bold">{{texts.text1}}</h1>
                        <hr class="divider my-4" />
                    </div>
                    <div class="col-lg-8 align-self-baseline">
                        <p @click="()=>textToEdit = 'text2'" class="text-white-75 font-weight-light mb-5">{{texts.text2}}</p>
                        <a :style="'background-color:'+theme" style="color:#fff" @click="()=>textToEdit = 'text3'" class="btn btn-primary btn-xl js-scroll-trigger">{{texts.text3}}</a>
                    </div>
                </div>
            </div>
        </header>
        <!-- About section-->
        <section :style="'background:'+theme" class="page-section bg-mid" id="about">
            <div class="container">
                <div class="row justify-content-center">
                    <div class="col-lg-8 text-center">
                        <h2 @click="()=>textToEdit = 'text4'" class="text-white mt-0">{{texts.text4}}</h2>
                        <hr class="divider light my-4" />
                        <p @click="()=>textToEdit = 'text5'" class="text-white-50 mb-4">{{texts.text5}}</p>
                        <a :style="'color:'+theme" @click="()=>textToEdit = 'text6'" class="btn btn-light btn-xl js-scroll-trigger" style="color:#fff">{{texts.text6}}</a>
                    </div>
                </div>
            </div>
        </section>
        <!-- Services section-->
        <section class="page-section" id="services">
            <div class="container">
                <h2 @click="()=>textToEdit = 'text7'" class="text-center mt-0">{{texts.text7}}</h2>
                <hr class="divider my-4" />
                <div class="row">
                    <div class="col-lg-3 col-md-6 text-center">
                        <div class="mt-5">
                            <i :style="'color:'+theme" class="fas fa-4x fa-gem"></i>
                            <h3 @click="()=>textToEdit = 'text8'" class="h4 mb-2">{{texts.text8}}</h3>
                            <p @click="()=>textToEdit = 'text9'" class="text-muted mb-0">{{texts.text9}}</p>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6 text-center">
                        <div class="mt-5">
                            <i :style="'color:'+theme" class="fas fa-4x fa-laptop-code"></i>
                            <h3 @click="()=>textToEdit = 'text10'" class="h4 mb-2">{{texts.text10}}</h3>
                            <p @click="()=>textToEdit = 'text11'" class="text-muted mb-0">{{texts.text11}}</p>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6 text-center">
                        <div class="mt-5">
                            <i :style="'color:'+theme" class="fas fa-4x fa-globe"></i>
                            <h3 @click="()=>textToEdit = 'text12'" class="h4 mb-2">{{texts.text12}}</h3>
                            <p @click="()=>textToEdit = 'text13'" class="text-muted mb-0">{{texts.text13}}</p>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6 text-center">
                        <div class="mt-5">
                            <i :style="'color:'+theme" class="fas fa-4x fa-heart"></i>
                            <h3 @click="()=>textToEdit = 'text14'" class="h4 mb-2">{{texts.text14}}</h3>
                            <p @click="()=>textToEdit = 'text15'" class="text-muted mb-0">{{texts.text15}}</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <!-- Portfolio section-->
        <section id="portfolio">
            <h2 class="text-center mt-0">Categories</h2>
            <hr class="divider my-4" />
            <div class="container-fluid p-0">
                <div class="row no-gutters">
                    <div :key="cat.category" v-for="cat in categories" class="col-lg-4 col-sm-6">
                        <a @click="()=>gotoitems(cat.category)" class="portfolio-box"
                            ><img class="img-fluid" :src="cat.image" alt="" />
                            <div class="portfolio-box-caption">
                                <div class="project-category text-white-50">Category</div>
                                <div class="project-name">{{cat.category}}</div>
                            </div></a
                        >
                    </div>
                </div>
            </div>
        </section>
        <!-- Call to action section-->
        <section class="page-section bg-dark text-white">
            <div class="container text-center">
                <h2 style="color:#fff" @click="()=>textToEdit = 'text16'" class="mb-4">{{texts.text16}}</h2>
                <a @click="()=>textToEdit = 'text17'" class="btn btn-light btn-xl" :href="texts.text17"><strong @click="()=>textToEdit = 'text18'">{{texts.text18}}</strong></a>
            </div>
        </section>
        <!-- Contact section-->
        <section class="page-section" id="contact">
            <div class="container">
                <div class="row justify-content-center">
                    <div class="col-lg-8 text-center">
                        <h2 @click="()=>textToEdit = 'text19'" class="mt-0">{{texts.text19}}</h2>
                        <hr class="divider my-4" />
                        <p @click="()=>textToEdit = 'text20'" class="text-muted mb-5">{{texts.text20}}</p>
                    </div>
                </div>
                <div class="row">
                    <div @click="()=>textToEdit = 'text21'" class="col-lg-3 ml-auto text-center mb-3 mb-lg-0">
                        <i class="fas fa-phone fa-3x mb-3 text-muted"></i>
                        <div>{{texts.text21}}</div>
                    </div>
                    <div @click="()=>textToEdit = 'text22'" class="col-lg-3 mr-auto text-center">
                        <i class="fas fa-envelope fa-3x mb-3 text-muted"></i
                        ><!-- Make sure to change the email address in BOTH the anchor text and the link target below!--><a class="d-block" :href="texts.text22">Email</a>
                    </div>
                    <div @click="()=>textToEdit = 'text23'" class="col-lg-3 mr-auto text-center">
                        <i class="fab fa-facebook-f fa-3x mb-3 text-muted"></i
                        ><!-- Make sure to change the email address in BOTH the anchor text and the link target below!--><a class="d-block" :href="texts.text23">Facebook</a>
                    </div>
                    <div @click="()=>textToEdit = 'text24'" class="col-lg-3 mr-auto text-center">
                        <i class="fab fa-instagram-square fa-3x mb-3 text-muted"></i
                        ><!-- Make sure to change the email address in BOTH the anchor text and the link target below!--><a class="d-block" :href="texts.text24">Instgram</a>
                    </div>
                </div>
            </div>
        </section>
        <input class="editInput form-control" v-if="textToEdit" type="text" v-model="texts[textToEdit]"/>
        <div class="card">
            <input class="picker" type="color" v-model="theme"/>
        </div>
        <button @click="updateInfo" class="btn btn-primary update">Update</button>
    </body>
</template>
<script>
import axios from 'axios'
export default {
    mounted() {
        axios.get('/getInfo').then(res => {
            const info = {...res.data.info};
            this.theme = info.theme;
            this.url = info.image;
            this.file = info.image;
            delete info['theme'];
            delete info['image'];
            this.texts = info;
        })
    },
    data() {
        return {
            textToEdit: null,
            theme:'red',
            url: null,
            file: null,
            categories: [],
            texts: {
                text1: 'Heading1_1',
                text2: 'Heading2_1',
                text3: 'Button1',
                text4: 'Button1',
                text5: 'Button1',
                text6: 'Button1',
                text7: 'Button1',
                text8: 'Button1',
                text9: 'Button1',
                text10: 'Button1',
                text11: 'Button1',
                text12: 'Button1',
                text13: 'Button1',
                text14: 'Button1',
                text15: 'Button1',
                text16: 'Button1',
                text17: 'Button1',
                text18: 'Button1',
                text19: 'Button1',
                text20: 'Button1',
                text21: 'Button1',
                text22: 'Button1',
                text23: 'Button1',
                text24: 'Button1',
                text25: 'nav1',
                text26: 'nav2',
                text27: 'nav3',
                text28: 'nav4',
            }
        }
    },
    methods: {
        imagePrev(event) {
            const file = event.target.files[0];
            const fileTypes = ['image/jpeg', 'image/jpg', 'image/png'];
            console.log(file.type, file.size);
           if(!fileTypes.includes(file.type)) {
               this.$store.dispatch('writemessage', 'the file not type not allowed');
               return;
           }
           if(file.size > 400000) {
               this.$store.dispatch('writemessage', 'the file size is larger than allowed');
               return;
           }
           this.url = URL.createObjectURL(file);
           this.file = file;
           this.progress = 0;
        },
        updateInfo() {
            const formData = new FormData();
            formData.append('theme', this.theme);
            const texts = {...this.texts};
            Object.keys(texts).map((tx, i) => {
                formData.append(tx, texts[tx]);
            });
            formData.append('file', this.file);
            console.log('hereeeee');
            axios.post('/updateInfo', formData).then(res => {
                this.$store.dispatch('writemessage', res.data.message);
            })
        },
    }
}
</script>

<style scoped lang="scss">
    $bgPrimary: rgb(10, 90, 28);
    @import '../../scss/main.scss';
    
    header.masthead {
    padding-top: 10rem;
    padding-bottom: calc(10rem - 4.5rem);
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;
    }
    .editInput {
        position: fixed;
        left: 40%;
        bottom:20%;
        width: 30%;
        background-color: #9ca6aa
    }
    .picker {
        position: fixed;
        left: 30%;
        top: 40%;
    }
    .update {
        position: fixed;
        left: 30%;
        top: 45%;
    }
</style>