<template>
    <v-sheet
      class="mx-auto ma-6"
      elevation="8"
      max-width="90%"
    >
      <h3 class="text-center">{{title}}</h3>
      <v-slide-group
        v-model="model"
        class="pa-4"
        active-class="success"
        show-arrows
      >
        <v-slide-item
          v-for="item in items"
          :key="item._id"
          v-slot:default="{  }"
        >
            <v-row
              class="fill-height ma-4"
              height="200"
              width="100"
              align="center"
              justify="center"
            >
              <v-card link :to="'/details/'+item._id">
                <v-img :src="item.image" height="200" width="150"></v-img>
                <v-card-title>{{item.name}}</v-card-title>
                
                <v-card-text>
                  <v-row
                  align="center"
                  class="mx-0"
                   >
                    <v-rating
                      readonly
                      :value="item.rating"
                      size="20"
                      dense
                      background-color="orange lighten-3"
                      color="orange"
                    ></v-rating>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-row>
        </v-slide-item>
      </v-slide-group>
    </v-sheet>
</template>

<script>
export default {
mounted() {
  console.log(this.items);
},
  props: ['items', 'title']
}
</script>