<template>
    <div class="container">
        <div>
            <label><strong>Category name</strong></label>
            <input class="form-control" v-model="name" type="text" placeholder="Category name"/>
            <br>
            <div class="nochars form-group">
                <label><strong>Choose number of features of the items in this categories</strong></label>
                <input class="form-control" v-model="num" style="width:20%;" type="number" max="5" min="1"/>
            </div>
            <br>
            <input style="display:none" ref="img" @change="imagePrev" type="file"/>
            <div style="padding-top:auto" class="row">
                <div class="col-md-6 image" v-if="url">
                    <img :src="url" height="300px" width="100%"/>
                </div>
                <div style="margin-top:10%;height:110px;overflow:hidden" class="col-md-6">
                    <img style="cursor:pointer;width:20%;height:100%" @click="$refs.img.click()" src="../../assets/images/upload.png"/>
                </div>
            </div>
            <br>
            <h4><strong>Characterestics to add</strong></h4>
            <input style="margin:5px;width:60%" class="form-control" :key="i" v-for="(ch, i) in mode==='add'?arr:chars" v-model="chars[i]" type="text" :placeholder="'characterestic'+(i+1)"/>
            <button style="padding:10px 130px" class="btn btn-primary" @click="addcat">Add</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    created() {
        const params = {...this.$route.params};
        if(Object.keys(params).length > 0) {
            this.mode = 'update';
            const cat = params.category;
            this.name = cat;
            this.id = params._id;
            this.url = params.image?params.image.split('*').join('/'): null;
            this.file = params.image?params.image.split('*').join('/'): null;
            delete params['category'];
            delete params['_id'];
            delete params['image'];
            let chars = Object.keys(params).map(char => {
                if(params[char] !=='undefined') {
                    return params[char]
                }else {return null}
            });
            const chars2 = [];
            for(let ch of chars) {
                if(ch) {
                    chars2.push(ch);
                }
            }
            this.chars = chars2;
            this.num = chars2.length;
        }
    },
    data() {
        return {
            num: 1,
            name: '',
            id: '',
            chars: [],
            mode: 'add',
            url: null,
            file: null
        }
    },
    computed: {
        arr() {
            const arr = [];
            for(let i=0; i<this.num; i++) {
                arr.push(i);
            }
            return arr;
        }
    },
    watch: {
        num(val) {
            const chars = [...this.chars];
            this.chars = [];
            for(let i=0; i<val; i++) {
                this.chars.push(chars[i]);
            }
        }
    },
    methods: {
        addcat() {
            const formData = new FormData();
            formData.append('category', this.name);
            if(this.id !== '') formData.append('_id', this.id);
            const chars = [...this.chars];
            chars.map((ch, i) => {
                formData.append('char'+ (i+1), ch);
            });
            formData.append('file', this.file);
            const process = this.mode === 'add'?'/addcat': '/addcat/'+this.id;
            axios.post(process, formData).then(res => {
                this.$store.dispatch('writemessage', res.data.message);
            })
        },
        imagePrev(event) {
            const file = event.target.files[0];
            const fileTypes = ['image/jpeg', 'image/jpg', 'image/png'];
            console.log(file.type, file.size);
           if(!fileTypes.includes(file.type)) {
               this.$store.dispatch('writemessage', 'the file not type not allowed');
               return;
           }
           if(file.size > 400000) {
               this.$store.dispatch('writemessage', 'the file size is larger than allowed');
               return;
           }
           this.url = URL.createObjectURL(file);
           this.file = file;
           this.progress = 0;
        },
    }
}
</script>

