<template>
    <div class="container">
          <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn
            color="primary"
            dark
            v-on="on"
          >
            Chooose category
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            v-for="(cat, index) in cats"
            :key="index"
            @click="()=>getChars(cat.category)"
          >
            <v-list-item-title>{{ cat.category }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
        <div class="text-center border border-light p-5">

            <p class="h3 mb-4">Add Movie <strong style="color:#b34848">{{category.length>0?'in '+category:''}}</strong></p>

            <!--  -->
            <label class="labels">Name</label>
            <input v-model="name" type="text" class="form-control mb-4" placeholder="Name">
            <div :key="char" v-for="(char, i) in charsTags">
                <label class="labels">{{char}}</label>
                <input v-model="chars[i]" type="text" class="form-control mb-4" :placeholder="char">
            </div>
            <br>
            <div>
                <label class="labels">Price</label>
                <input v-model="price" type="number" class="form-control mb-4" placeholder="Price">
            </div>
            <!--  -->
            <label class="labels">Add description</label>
            <textarea class="form-control mb-4" v-model="description" ></textarea>
            <br>
            <div>
                <h4>Choose number of actors:</h4>
                <input min="1" style="width:30%" type="number" v-model="numberOfActors" class="form-control mb-4" placeholder="Number of actors"/>
            </div>
            <h4 class="h4 mb-4">Actors</h4>
            <div class="actor" :key="actor.name" v-for="(actor, i) in actors">
                <h4><strong>{{'Actor'+(i+1)}}</strong></h4>
                <label>Name</label>
                <input v-model="actors[i]['name']" type="text" class="form-control mb-4" :placeholder="actor.name || 'Actor name'">
                <label>Description</label>
                <input v-model="actors[i]['desc']" type="text" class="form-control mb-4" :placeholder="actor.desc || 'Actor description'">
                <label>Internet image</label>
                <input type="text" v-model="actors[i]['image']" class="form-control mb-4" :placeholder="actor.image || 'Actor image'"/>
                <image :src="actors[i]['image']" width="70px" height="70px"/>
            </div>
            <input style="display:none" ref="image" type="file" @change="imagePrev"/>
            <div class="d-flex justify-content-around row">
                <div class="iamge col-md-9">
                    <img height="500px" class="img" v-if="url" :src="url"/>
                </div>
                <div class="col-md-3" style="height:200px;">
                    <img style="margin-top:25%;cursor:pointer" height="80%" width="100%" @click="()=>$refs.image.click()" src="../../assets/images/upload.png"/>
                    <small>Upload image</small>
                </div>
            </div>
            <br>
            <h3 style="font-weight:bolder;color:#555">Upload video</h3>
            <div class="videos">
                <div @click="()=>playVideo(part)" v-for="part in partsKeys" :key="part" class="video" :class="+part===+episode?'active':''"><h4 style=""><strong>Episode({{part}})</strong>: {{parts[part]?parts[part].split('/')[4].split('.')[0]:''}}</h4></div>
                <video-player v-if="src" :src="src" />
            </div>
            <i style="font-size:40px;cursor:pointer;" class="fas fa-plus" @click="episode=noOfEpisodes+1;src=null"></i> New episode
            <div class="uploadVideo">
                <br><br>
                <h4>Upload episode {{episode}}</h4>
                <input type="file" @change="uploadVideo"/>
            </div>
            <button @click="addItem" class="btn btn-info btn-block my-4" type="submit">Add item</button>
            
            <div v-if="progress" class="progess-bar" :style="{'width': progress}">
                {{progress}}
            </div>
        </div>
    </div>
<!-- Default form login -->
</template>

<script>
import axios from 'axios'
import videoPlayer from '../../components/videoPlayer'
export default {
    components: {
        videoPlayer
    },
    created() {
        const params = {...this.$route.params};
        axios.get('/fetchCats').then(res => {
            console.log(res.data);
            this.cats = res.data.cats;
        })
        if(Object.keys(params).includes('id')) {
            this.mode = 'update';
            this.getData();
            return;
        }
    },
    data() {
        return {
            cats: [],
            name: '',
            description: '',
            mode: 'add',
            id: '',
            menu: false,
            category: '',
            chars: [],
            actors: [{name: 'Farghaly', image: 'https://scontent-hbe1-1.xx.fbcdn.net/v/t1.0-9/86933328_2853976698026624_3288826792655192064_n.jpg?_nc_cat=111&_nc_sid=85a577&_nc_oc=AQmIhIKIiLa8fFsA9D0Dmx9qJbqzylQGJqk-VZgKCGPIWPhswT_S0lehkA44XQDDWiI&_nc_ht=scontent-hbe1-1.xx&oh=ae44ca62dca03a755fd7c020e4d0e129&oe=5EC51DE8', desc:'actor'}],
            price: 0,
            charsTags: [],
            url: null,
            file: '',
            vFile: '',
            progress: 0,
            itemData: {},
            parts: {},
            episode: 1,
            src: null,
            noOfEpisodes: 1,
            numberOfActors: 0
        }
    },
    methods: {
        getData() {
            const params = {...this.$route.params};
            this.id = params.id;
            axios.get('/getItemData/'+this.id).then(res => {
            const itemData = res.data.itemData;
            this.id = itemData._id;
            this.itemData = itemData;
            this.getChars(this.itemData.category);
            this.name = itemData.name;
            this.description = itemData.description;
            this.url = itemData.image;
            this.file = itemData.image;
            this.parts = itemData.video;
            this.partsKeys = Object.keys(this.parts);
            this.episode = Object.keys(this.parts).length + 1;
            this.noOfEpisodes = Object.keys(this.parts).length;
            this.price = itemData.price;
            Object.keys(itemData).forEach(d => {
                if(d.includes('char')) {
                    this.chars.push(itemData[d]);
                }
            })
            this.actors = itemData.actors;
        })
        },
        getChars(cat) {
            this.category = cat;
            axios.get('/fetchChars/'+cat).then(res => {
                const charsTags = res.data.chars;
                delete charsTags['category'];
                delete charsTags['_id'];
                delete charsTags['image'];
                this.charsTags = Object.keys(charsTags).map(key => charsTags[key]);
            })
        },
        imagePrev(event) {
            const file = event.target.files[0];
            const fileTypes = ['image/jpeg', 'image/jpg', 'image/png'];
           if(!fileTypes.includes(file.type)) {
               this.$store.dispatch('writemessage', 'the file not type not allowed');
               return;
           }
           if(file.size > 400000) {
               this.$store.dispatch('writemessage', 'the file size is larger than allowed');
               return;
           }
           this.url = URL.createObjectURL(file);
           this.file = file;
           this.progress = 0;
        },
        playVideo(part) {
            this.episode=+part;
            this.src=null;
            setTimeout(()=>{
                this.src=this.parts[part]
            }, 200)
        },
        uploadVideo(event) {
            const file = event.target.files[0];
           if(file.type !== 'video/mp4') {
               this.$store.dispatch('writemessage', 'the video type not allowed');
               return;
           }
           if(file.size > 400000000) {
               this.$store.dispatch('writemessage', 'the video size is larger than allowed');
               return;
           }
           //this.url = URL.createObjectURL(file);
           this.vFile = file;
        },
        addItem() {
            const actorsArray = [...this.actors];
            actorsArray.map(act => {
                return JSON.stringify({...act})
            })
             if(this.mode === 'update') {
            //   axios.post('/updateAd/'+this.id, {text1: this.text1, text2: this.text2, description: this.description}).then(res => {
            //     this.$store.dispatch('writemessage', res.data.message);
            //     })  
            //     return;
            // }
            // axios.post('/addAd', {text1: this.text1, text2: this.text2, description: this.description}).then(res => {
            //     this.$store.dispatch('writemessage', res.data.message);
            // })
             }
            const formData = new FormData();
            formData.append('name', this.name);
            formData.append('category', this.category);
            formData.append('price', this.price);
            formData.append('description', this.description);
            this.chars.forEach((char, i) => {
                formData.append('char'+(i+1),char);
            });
            formData.append('actors', JSON.stringify(actorsArray));
            formData.append('image', this.file);
            formData.append('video', this.vFile);
            formData.append('episode', this.episode);
            const process = this.mode === 'update'?'/updateItem/'+this.id:'/uploadItem'
            axios.post(process, formData, {
                onUploadProgress: ProgressEvent => {
                    let progress =
                    Math.round((ProgressEvent.loaded / ProgressEvent.total) * 100)
                    +"%";
                    this.progress = progress;
                    if(this.progress === 100) {
                        this.progress = 0;
                    }
                }
            }).then(res => {
                this.$store.dispatch('writemessage', res.data.message);
                this.getData();
            })
        }
    },
    watch: {
        numberOfActors(val) {
            this.actors = [];
            for(let i=0; i<val; i++) {
                this.actors.push({});
            }
        }
    }
}
</script>

<style lang="scss">
    .progess-bar {
        background-color: green;
    }
    .actor {
        width: 90%;
        padding: 20px;
        background-color: #ccc;
        margin: 10px;
        h4 {
            color:rgb(102, 102, 104)
        }
        label {
            float: left;
            font-weight: bolder;
            color:rgb(72, 72, 75)
        }
    }
    .labels {
        float: left;
        font-weight: bolder;
        font-size: 20px;
        color:rgb(72, 72, 75)
    }
    .menu {
        width: 200px;
        padding: 10px;
        background-image: linear-gradient(rgb(206, 202, 202), #ccc, rgb(255, 255, 255));
        li {
            list-style: none;
            cursor: pointer;
            text-decoration: none;
            width: 100%;
            margin:5px;
            font-weight: bolder;
            color:rgb(92, 91, 91);
            :hover {
                padding-left: 20px;
            }
        }
    }
    .d-flex {
        width: 90%;
        padding: 10px;
    }
    .videos {
        width: 90%;
        padding: 10px;
        background-color: rgb(240, 233, 233);
        .video {
            margin: 10px;
            background-color: #ccc;
            padding: 5px 20px;
            cursor: pointer;

        }
        
        .active {
            background-color: rgb(46, 52, 87);
            color: #fff;
        }
    }
    .progess-bar {
        color: #fff;
        font-size: 20px;
        font-weight: bolder;
        border-radius: 20px;
    }
</style>