<template>
      <v-container>
        <v-row>
          <v-col
            cols="12"
            md="4"
          >
           <v-select
            @change="() => setItemsPerPage(this.numOfItems)"
            :items="[6,9,12,15,18,21,24]"
            label="Num of rows"
          ></v-select>
          </v-col>
  
          <v-col
            cols="12"
            md="4"
          >
            <v-text-field v-model="searchQuery" @input="search" label="Search"></v-text-field>
          </v-col>
  
          <v-col
            cols="12"
            md="4"
          >
            <v-select
             @change="orderHandler"
            :items="['Newest', 'Best rating']"
            label="Sort by"
          ></v-select>
          </v-col>
        </v-row>
      </v-container>
</template>

<script>
//import debounce from 'debounce';

export default {
    created() {
       //this.search = setTimeout(this.search, 1000);
    },
    props: ['setItemsPerPage'],
    data() {
        return {
            numOfItems: 9,
            order: 'newest',
            searchQuery: ''
        }
    },
    methods: {
        orderHandler() {
            this.$store.dispatch('orderBy', this.order);
        },
        search(event) {
            this.$store.dispatch('search', this.searchQuery);
        }
    }
}
</script>

<style scope>
    .containerr{
        margin-top:30px;
        display: flex;
        justify-content: center
    }
    .filter {
        width: 100%;
    }
    .filter-col{
    /* padding-left:10px;
    padding-right:10px; */
    }
    .filterRow {
        display: flex;
        flex-direction: row;
        justify-content: left;
        align-items: center;
    }
    .filterRow label{
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        color:rgb(134, 128, 133)
    }
    .filterRow input {
        border-bottom-color: #ccc;
        border-bottom-width: 2px;
    }
</style>