<template>
    <div class="container">
      
         <app-modal v-if="modal">
            <h3 class="login" slot="header">Write "confirm" to confirm delete</h3>
            <div class="modalBody" slot="body">
                <input type="text" class="form-control" ref="password"/>
                <button @click="deleteCat" class="btn btn-primary">Confirm</button>
                <button @click="modal=false" class="btn btn-primary">close</button>
            </div>
        </app-modal>
        <h3 class="center">Categories</h3>
<!-- Card deck -->
        <v-row class="mb-6">
            <v-col v-for="(cat, i) in cats" :key="i" :md="4" :sm="12">
                <v-card
                class="mx-auto col-md-3"
                slot="progress"
                max-width="400"
                :loading="loading"
                >
                <v-img
                    class="white--text align-end"
                    height="200px"
                    :src="cat.image"
                >
                    <v-card-title>{{cat.category}}</v-card-title>
                </v-img>
            
                <v-card-title>{{cat.category}}</v-card-title>
            
                <v-card-actions>
                    <v-btn
                    color="#3f608e"
                    text
                    @click="()=>editCat(cat)"
                    >
                    Edit
                    </v-btn>
            
                    <v-btn
                    color="#af2828e3"
                    text
                    @click="()=>{catId=cat._id;modal=true}"
                    >
                    Delete
                    </v-btn>
                </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </div>
</template>

<script>
import axios from 'axios';
import modalVue from '../../components/modal.vue';
export default {
    components: {
        appModal: modalVue,
    },
    created() {
        this.loading = true;
        axios.get('/getCats').then(res => {
            this.cats = res.data.cats;
            this.loading = false
        })
    },
    data() {
        return {
            cats: [],
            modal: false,
            catId: 0,
            loading: false
        }
    },
    methods: {
        editCat(cat) {
            this.$router.push({path: '/dashboard/editcat/'+cat._id+'/'+cat.category+'/'+cat.char1+'/'+cat.char2+'/'+cat.char3+'/'+cat.char4+'/'+cat.char5+'/'+cat.image.split('/').join('*')});
        },
        deleteCat() {
            if(this.$refs.password.value === 'confirm') {
                axios.get('/deleteCat/'+this.catId).then(res => {
                    this.cats = res.data.cats
                    this.modal = false;
                });
            } else {
               this.$store.dispatch('writemessage', 'password isn\'t correct...'); 
            }
        }
    }
}
</script>

<style scoped lang="scss">
   .card{
  margin: 5% 0%;
}

.card-body{
  margin: 0% 0% 0% 3%;
  padding: 6% 0%;
}


</style>