
<template>
    <div class="container">
        <form @submit.prevent="addAd" class="text-center border border-light p-5">

            <p class="h4 mb-4">Add an advertisment</p>

            <!-- Email -->
            <input v-model="text1" type="text" id="defaultLoginFormEmail" class="form-control mb-4" placeholder="Text1">

            <!-- Password -->
            <input v-model="text2" type="text" id="defaultLoginFormPassword" class="form-control mb-4" placeholder="Text2">
            <label>Add description</label>
            <textarea class="form-control mb-4" v-model="description"></textarea>
            <input style="display:none" ref="img" @change="imagePrev" type="file"/>
            <div style="padding-top:auto" class="row">
                <div class="col-md-6 image" v-if="url">
                    <img :src="url" height="300px" width="100%"/>
                </div>
                <div style="margin-top:10%;height:110px;overflow:hidden" class="col-md-6">
                    <img style="cursor:pointer;width:20%;height:100%" @click="$refs.img.click()" src="../../assets/images/upload.png"/>
                </div>
            </div>
            <div class="d-flex justify-content-around">
            </div>

            <!-- Sign in button -->
            <button class="btn btn-info btn-block my-4" type="submit">Add ad</button>

        </form>
    </div>
<!-- Default form login -->
</template>

<script>
import axios from 'axios'
export default {
    created() {
        const params = {...this.$route.params};
        if(Object.keys(params).length > 0) {
            this.mode = 'update';
            this.id = params.id;
            axios.get('/getAdData/'+params.id).then(res => {
                console.log(res.data)
                this.text1 = res.data.adData.text1;
                this.text2 = res.data.adData.text2;
                this.description = res.data.adData.description;
                this.file = res.data.adData.image;
                this.url = res.data.adData.image;
            })
        }
    },
    data() {
        return {
            text1: '',
            text2: '',
            description: '',
            id: '',
            url: null,
            file: null
        }
    },
    methods: {
        addAd() {
            const formData = new FormData();
            formData.append('text1', this.text1);
            formData.append('text2', this.text2);
            formData.append('description', this.description);
            formData.append('file', this.file);
            if(this.mode === 'update') {
              axios.post('/updateAd/'+this.id, formData).then(res => {
                this.$store.dispatch('writemessage', res.data.message);
                })  
                return;
            }
            axios.post('/addAd', formData).then(res => {
                this.$store.dispatch('writemessage', res.data.message);
            })
        },
        imagePrev(event) {
            const file = event.target.files[0];
            const fileTypes = ['image/jpeg', 'image/jpg', 'image/png'];
            console.log(file.type, file.size);
           if(!fileTypes.includes(file.type)) {
               this.$store.dispatch('writemessage', 'the file not type not allowed');
               return;
           }
           if(file.size > 400000) {
               this.$store.dispatch('writemessage', 'the file size is larger than allowed');
               return;
           }
           this.url = URL.createObjectURL(file);
           this.file = file;
           this.progress = 0;
        },
    }
}
</script>

