<template>
    <div class="container">
        <h2 style="margin:30px">{{actor}}</h2>
        <section>
            <app-loading v-if="loading" />
            <div class="row">
                <div class="col-md-3">
                    <div :key="item.name" v-for="item in movies" class="col-sm-6 col-md-4">
                        <app-item :wishlist="wishlist" :itemDetails="item" ></app-item>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import Item from '../components/Item.vue'
import Loading from '../components/loading.vue'
import axios from 'axios'
export default {
    async created() {
                        console.log('kokoko',this.movies);

        const actor = this.$route.params.name;
        this.actor = actor
        this.loading = true;
        const movies = await axios.get('/getActorMovies/'+actor);
        this.loading = false;
        this.movies = movies.data.actorMovies;
        this.$store.dispatch('getwishlist');

    },
    data() {
        return {
            movies: [],
            actor: '',
            loading: false
        }
    },
    computed: {
        wishlist() {
            return this.$store.getters.wishlist;
        }
    },
    components: {
        appItem: Item,
        appLoading: Loading
    }
}
</script>