<template>

    <div class="container">
        <app-modal v-if="modal">
            <h3 class="login" slot="header">Write "confirm" to confirm delete</h3>
            <div slot="body">
                <input type="text" class="form-control" ref="password"/>
                <button @click="deleteAd" class="btn btn-primary">Confirm</button>
            </div>
        </app-modal>
        <h3 class="center">Ads</h3>
        <div class="row">
                <!-- <div class="card" :key="i" v-for="(ad, i) in ads">
                    <h3 @click="() => editAd(ad)">{{ad.text1}}</h3>
                    <h3>{{ad.text2}}</h3>
                    <h3 @click="()=>{adId = ad._id;modal=true}" style="cursor:pointer">Delete<i class="fa fa-times"></i></h3>
                </div> -->
                    <div xlass="row">
                        <div style="margin:20px;hight:300px;overflow:auto" class="col-md-12" :key="i" v-for="(ad, i) in ads">
                            <b-card
                                overlay
                                :img-src="ad.image?ad.image:'https://picsum.photos/900/250/?image=3'"
                                img-alt="Card Image"
                                text-variant="white"
                                :title="ad.text1"
                                :sub-title="ad.text2"
                            >
                                <b-card-text>
                                {{ad.description}}
                                </b-card-text>
                                <b-card-footer>
                                    <button @click="()=>editAd(ad)" class="btn btn-primary">Edit</button>
                                    <button @click="()=>{adId = ad._id;modal=true}" class="btn btn-danger">Delete</button>
                                </b-card-footer>
                            </b-card>
                        </div>
                    </div>
    </div>
    </div>
</template>

<script>
import axios from 'axios';
import Modal from '../../components/modal.vue'
export default {
    components: {
        appModal: Modal
    },
    created() {
        axios.get('/getAds').then(res => {
            this.ads = res.data.ads;
            console.log(this.ads)
        })
    },
    data() {
        return {
            ads: [],
            modal: false,
            adId: null
        }
    },
    methods: {
        editAd(ad) {
            this.$router.push({path: '/dashboard/editad/'+ad._id});
        },
        async deleteAd() {
                if(this.$refs.password.value === 'confirm') {
                    const ads = await axios.get('/deleteAd/'+this.adId);
                    this.ads = ads.data.ads;
                    this.modal = false;
            } else {
                this.$store.dispatch('writemessage', 'password isn\'t correct...');
            }
        },
    }
}
</script>

<style scoped lang="scss">
    .card {
        padding: 0px;
        margin: 5px;
        height: 200px;
        border-radius: 20px;
        background-image: linear-gradient(#ccc, #ccc, #fff);
    }
</style>