      </b-carousel-slide>
    </b-carousel>
    </div> -->
    <!-- انا بحبك اكتر بموت فى حبك علم قلبي انتا مقلتش ولا ليا الا هيا خد قلبي معاه الله لا يحرمني منك بتخبي ليه خليك معايا روحي مرتاحالك  -->

    
    <v-carousel
      hide-delimiter-background
      show-arrows-on-hover
      continuous
      cycle
      height="200"
      :max-width="width"
      >
      <v-carousel-item
        v-for="ad in ads" 
        :key="ad._id"
        :src="ad.image"
        reverse-transition="fade-transition"
        transition="fade-transition"
      >
            <v-row align="center" justify="center">
              <div style="color:#fff;text-align:center;margin-top:auto;margin-bottom:auto;">
              <h2>{{ad.text1}}</h2>
              <h3>{{ad.text2}}</h3>
              <h4>{{ad.description}}</h4>
              </div>
            </v-row>
      </v-carousel-item>
    </v-carousel>
</template>

<script>
  import axios from 'axios'
  export default {
    props: ['width'],
    created() {
        axios.get('/getAds').then(res => {
            this.ads = res.data.ads;
            console.log(this.ads);
        })
      },
    data() {
      return {
        slide: 0,
        sliding: null,
        ads: []
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      }
    }
  }
</script>