import Vue from 'vue'
import VueRouter from 'vue-router'
import Categories from '../views/categories.vue'
import wishlist from '../views/wishlist.vue'
import Items from '../views/items.vue'
import Details from '../views/details.vue'
import actorMovies from '../views/actorMovies.vue'
import dashboard from '../views/dashboard.vue'
import addCategory from '../views/admin/addCategory.vue'
import chatRoom from '../views/admin/chatRoom.vue'
import carts from '../views/admin/carts.vue'
import users from '../views/admin/users.vue'
import ads from '../views/admin/ads.vue'
import items from '../views/admin/items.vue'
import addItem from '../views/admin/addItem.vue'
import Header from '../components/header/header.vue'
import Audio from '../components/audio.vue'
import addAd from '../views/admin/addAd.vue'
import Redirector from '../views/redirector.vue'
import mainDashboard from '../views/admin/mainDashboard.vue'
import adminCategories from '../views/admin/categories.vue'
import mainPageEditor from '../views/admin/mainPageEditor.vue'
import store from '../store/index'
Vue.use(VueRouter)

const routes = [
  { path: '/', component: Categories },
  { path: '/redirector/:path', component: Redirector },
  { path: '/audio', component: Audio },
  { path: '/actorMovies/:name', components: {default: actorMovies, 'header': Header} },
  
  { path: '/dashboard',
   components:{default: dashboard},
   children: [{
     path: '',
     component: mainDashboard
   },{
     path: 'addcat',
     component: addCategory
   },{
    path: 'editcat/:_id/:category/:char1/:char2/:char3/:char4/:char5/:image',
    component: addCategory
  },{
    path: 'admincategories',
    component: adminCategories
  },{
    path: 'carts',
    component: carts
  },{
    path: 'chatroom',
    component: chatRoom
  },{
    path: 'users',
    component: users
  },{
    path: 'ads',
    component: ads
  },{
    path: 'addad',
    component: addAd
  },{
    path: 'editad/:id',
    component: addAd
  },{
    path: 'items',
    component: items
  },{
    path: 'additem',
    component: addItem
  },{
    path: 'edititem/:id',
    component: addItem
  },{
    path: 'editMainPage',
    component: mainPageEditor
  }]
  , beforeEnter: (to, from, next) => {
    if(localStorage.getItem('role') == 1) {
      next();
    } else {
      next('/');
      store.state.showRegisterModal = true;
    }
  } },





  { path: '/wishlist', components:{default: wishlist, 'header': Header} , beforeEnter: (to, from, next) => {
    if(localStorage.getItem('token')) {
      next();
    } else {
      next('/');
      store.state.showRegisterModal = true;
    }
  } },
  { path: '/items/:cat',  components:{default: Items, 'header': Header} },
  { path: '/details/:id', components:{default: Details, 'header': Header} },
  // {
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
